import { defineComponent } from '@antiquemouse/framework'
import { authManager } from '../../modules/authManager'
import { profileApi } from '../../modules/profileApi'
import { Button } from '../Button/Button'
import styles from './AvatarForm.module.scss'

interface AvatarFormState {
	selectedFile: File | null
	previewUrl: string
	error: string
	isSubmitting: boolean
	currentAvatar: string
	_isMounted: boolean
	avatarVersion: number
}

const IMAGE_SIZE = 10 * 1024 * 1024

export const AvatarForm = defineComponent({
	state(): AvatarFormState {
		return {
			selectedFile: null,
			previewUrl: '',
			error: '',
			isSubmitting: false,
			currentAvatar: '',
			_isMounted: false,
			avatarVersion: 0,
		}
	},

	async onMounted() {
		this.updateState({ _isMounted: true })
		await this.loadUserProfile()
	},

	onUnmounted() {
		if (this.state.previewUrl) {
			URL.revokeObjectURL(this.state.previewUrl)
		}
		this.state._isMounted = false
	},

	async loadUserProfile() {
		const user = authManager.getUser()
		if (!user) {
			return
		}

		try {
			const response = await profileApi.getProfile('me')

			if (!this.state._isMounted) {
				return
			}

			if (response.service.success && response.body.avatar_url) {
				let avatarUrl = response.body.avatar_url

				if (avatarUrl.includes('localhost:8081')) {
					avatarUrl = avatarUrl.replace('localhost:8081', '90.156.218.233:8081')
				}

				this.updateState({
					currentAvatar: avatarUrl,
					avatarVersion: this.state.avatarVersion + 1,
				})
			}
		} catch (error) {
			if (this.state._isMounted) {
				this.updateState({ error: '   ' })
			}
		}
	},

	handleFileChange(e: Event) {
		const file = (e.target as HTMLInputElement).files?.[0]

		if (!file) {
			this.updateState({
				selectedFile: null,
				previewUrl: '',
				error: '',
			})
			return
		}

		if (file.size > IMAGE_SIZE) {
			this.updateState({
				error: '     10MB',
				selectedFile: null,
				previewUrl: '',
			})
			return
		}

		if (!file.type.startsWith('image/')) {
			this.updateState({
				error: '  ',
				selectedFile: null,
				previewUrl: '',
			})
			return
		}

		if (this.state.previewUrl) {
			URL.revokeObjectURL(this.state.previewUrl)
		}

		const previewUrl = URL.createObjectURL(file)
		this.updateState({
			selectedFile: file,
			previewUrl,
			error: '',
		})
	},

	async handleSubmit(e: Event) {
		e.preventDefault()
		const { selectedFile } = this.state

		if (!selectedFile) {
			this.updateState({ error: ' ' })
			return
		}

		const user = authManager.getUser()
		if (!user) {
			this.updateState({ error: '  ' })
			return
		}

		this.updateState({ isSubmitting: true, error: '' })

		try {
			const uploadResponse = await profileApi.uploadAvatar('me', selectedFile)

			if (!this.state._isMounted) {
				return
			}

			if (uploadResponse.service.success && uploadResponse.body.avatar_url) {
				let avatarUrl = uploadResponse.body.avatar_url

				if (avatarUrl.includes('localhost:8081')) {
					avatarUrl = avatarUrl.replace('localhost:8081', '90.156.218.233:8081')
				}
				avatarUrl = avatarUrl.replace('90.156.218.233:8081', '90.156.218.233:8081/api/v0')

				this.updateState({
					currentAvatar: avatarUrl,
					avatarVersion: this.state.avatarVersion + 1,
					selectedFile: null,
					previewUrl: '',
				})
			} else {
				this.updateState({
					error: uploadResponse.service.error || '   ',
				})
			}
		} catch (error) {
			if (this.state._isMounted) {
				this.updateState({
					error: '    ',
				})
			}
		} finally {
			if (this.state._isMounted) {
				this.updateState({ isSubmitting: false })
			}
		}
	},

	render() {
		const {
			selectedFile,
			previewUrl,
			error,
			isSubmitting,
			currentAvatar,
			avatarVersion,
		} = this.state

		const getAvatarUrl = () => {
			if (!currentAvatar) return ''
			const separator = currentAvatar.includes('?') ? '&' : '?'
			return `${currentAvatar}${separator}t=${Date.now()}&v=${avatarVersion}`
		}

		const avatarUrl = getAvatarUrl()

		return (
			<form
				class={styles.avatar}
				on={{ submit: (e: Event) => this.handleSubmit(e) }}
			>
				<label class={styles.avatar__preview}>
					<input
						type="file"
						accept="image/*"
						on={{ change: (e: Event) => this.handleFileChange(e) }}
						disabled={isSubmitting}
						class={styles.avatar__input}
					/>

					{previewUrl ? (
						<img
							src={previewUrl}
							alt=" "
							class={styles.avatar__image}
							key={`preview-${previewUrl}`}
						/>
					) : currentAvatar ? (
						<img
							src={avatarUrl}
							alt=" "
							class={styles.avatar__image}
							key={`avatar-${avatarVersion}`}
							onError={e => {
								const target = e.currentTarget as HTMLImageElement
								target.style.display = 'none'
							}}
						/>
					) : (
						<div class={styles.avatar__placeholder}>
							<span class={styles.avatar__imageIcon}>+</span>
							<div> </div>
						</div>
					)}
				</label>

				{error && <div class={styles.avatar__error}>{error}</div>}

				{selectedFile && (
					<div class={styles.avatar__actions}>
						<Button
							type="submit"
							variant="accent"
							text={isSubmitting ? '...' : ''}
							disabled={isSubmitting}
						/>
						<Button
							type="button"
							variant="error"
							text=""
							disabled={isSubmitting}
							onClick={() => {
								if (this.state.previewUrl) {
									URL.revokeObjectURL(this.state.previewUrl)
								}
								this.updateState({
									selectedFile: null,
									previewUrl: '',
									error: '',
								})
							}}
						/>
					</div>
				)}
			</form>
		)
	},
})
